@-webkit-keyframes splash {
  0% {
    width: 0%;
    height: 0%;
    opacity: 1;}
  90% {
    width: 100%;
    height: 100%;
    opacity: 0;}
  }


  @keyframes splash {
  0% {
    width: 0%;
    height: 0%;
    opacity: 1;}
  90% {
    width: 100%;
    height: 100%;
    opacity: 0;}
  }


  @-webkit-keyframes splashRotate {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);}
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);}
    }


  @keyframes splashRotate {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);}
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);}
    }



